.MainPage {
  font-family: sans-serif;
  height: 400px;
  border-radius: 25px;
  background-color: #ffffff;
  overflow: hidden;
  outline: 2px solid red;
}
/* Top Header component---------------------------------------------------------------- */
.TopHeader {
  display: flex;
  padding: 20px 10px 10px 10px;
  background-color: #91A3B0;
}
#hamburger, #RadhaGram, #search {
  font-size: 30px;
}
#RadhaGram {
  margin-left: 1rem;
  font-size: 20px;
}
#search {
  margin-left: auto;
}
#menu {
  background-color: #91A3B0;
  width: 60%;
  height: 100%;
  position: fixed;
  top: -15px;
  left: -100%;
  color: #fff;
}

/* People Title Component --------------------------------------------------*/
.PeopleTitle {
  display: flex;
  padding: 10px 10px 5px 10px;

  flex-direction: row;
  outline: 2px solid red;
}
.PeopleTitle #circle {
  width: 54px;
  height: 54px;
  line-height: 54px;
  border-radius: 50%;
  font-size: 18px;
  font-weight: 500;
  color: #fff;
  text-align: center;
  background: purple;
  display: inline;
  float: left;
}
.PeopleTitle #Ladli {
  font-size: 18px;
  font-weight: 500;
  align-self: flex-start;
  margin: 0.3rem 1rem 1rem 1rem;
}
#LastMsg {
  color: gray;
  margin: 1.8rem 1rem 1rem -3.4rem;
}
#BlueTick {
  margin-left: auto;
  margin-top: 0.5rem;
}
